.widget-wrapper {
  position: relative;
  border: none !important;
}

/* ========================================================================
 * OVERVIEW
 * ======================================================================== */
.widget-overview .overview-label {
  text-transform: uppercase;
  font-size: 11px;
  opacity: 0.75;
  display: block;
  line-height: normal;
  margin-bottom: 2px;
}
.widget-overview h1 {
  margin: 0;
  line-height: 36px;
  font-size: 38px;
}
.widget-overview .row {
  margin-bottom: 10px;
}
.widget-overview .row:last-child {
  margin-bottom: 0px;
}
.widget-overview h4 {
  margin-top: 2px;
  margin-bottom: 0px;
}

/* ========================================================================
 * MINI STAT
 * ======================================================================== */
.mini-stat {
  padding: 15px;
  margin-bottom: 20px;
}

.mini-stat-chart {
  width: auto;
  height: 60px;
  display: inline-block;
  line-height: 60px;
  text-align: center;
  float: left;
  margin-right: 10px;
}

.mini-stat-icon {
  width: 60px;
  height: 60px;
  display: inline-block;
  line-height: 60px;
  text-align: center;
  font-size: 30px;
  background: none repeat scroll 0% 0% #EEE;
  border-radius: 100%;
  float: left;
  margin-right: 10px;
  color: #FFF;
}

.mini-stat-info {
  font-size: 12px;
  padding-top: 2px;
}
.mini-stat-info span {
  display: block;
  font-size: 30px;
  font-weight: 600;
  margin-bottom: 5px;
  margin-top: 7px;
}

/* ========================================================================
 * MINI STAT TYPE 3
 * ======================================================================== */
.mini-stat-type-3 {
  position: relative;
  background-color: #FBFBFB;
  padding: 10px;
  margin-bottom: 20px;
}
.mini-stat-type-3 h3 {
  margin-top: 8px;
}
.mini-stat-type-3 .meta-stat {
  display: block;
  border-top: 1px solid #DDD;
  font-size: 10px;
  color: #A7A7A7;
  padding-top: 5px;
  margin: 0 40px;
}

/* ========================================================================
 * MINI STAT TYPE 4
 * ======================================================================== */
.mini-stat-type-4 {
  text-align: center;
  margin-bottom: 20px;
  background-color: #ffffff;
  padding: 20px;
}
.mini-stat-type-4 h1, .mini-stat-type-4 h3 {
  margin-top: 0;
  font-weight: 300;
}
.mini-stat-type-4 h1 {
  font-size: 100px;
}

/* ========================================================================
 * MINI STAT TYPE 5
 * ======================================================================== */
.mini-stat-type-5 {
  text-align: center;
  margin-bottom: 20px;
  background-color: #ffffff;
  padding: 20px;
}
.mini-stat-type-5.border-facebook {
  border-top: 5px solid #3b5998;
}
.mini-stat-type-5.border-twitter {
  border-top: 5px solid #00a0d1;
}
.mini-stat-type-5.border-youtube {
  border-top: 5px solid #c4302b;
}
.mini-stat-type-5 h1, .mini-stat-type-5 h3 {
  margin-top: 0;
  font-weight: 300;
}
.mini-stat-type-5 h3 {
  font-size: 18px;
}
.mini-stat-type-5 h3 .label {
  display: inline-block;
  padding: 0;
  font-size: 10px;
  vertical-align: middle;
  padding-top: 2px;
}
.mini-stat-type-5 h1 {
  font-size: 35px;
  margin: 25px 0;
  position: relative;
}
.mini-stat-type-5 h1 .status, .mini-stat-type-5 h1 .number {
  display: inline-block;
}
.mini-stat-type-5 h1 .status {
  font-size: 15px;
  vertical-align: middle;
  margin-left: -55px;
}
.mini-stat-type-5 h1 .status .percent, .mini-stat-type-5 h1 .status i {
  display: block;
}
.mini-stat-type-5 p {
  margin: 0;
  font-size: 12px;
}

/* ========================================================================
 * MINI STAT TYPE 2
 * ======================================================================== */
.mini-stat-type-2 {
  position: relative;
  background-color: #FBFBFB;
  padding: 10px;
  margin-bottom: 20px;
}
.mini-stat-type-2 h3, .mini-stat-type-2 p {
