}
.mini-stat-type-2.border-danger {
  border-top: 15px solid #E9573F;
}
.mini-stat-type-2.border-success {
  border-top: 15px solid #8CC152;
}
.mini-stat-type-2.border-primary {
  border-top: 15px solid #00B1E1;
}
