/* ========================================================================
 * LOADING ANIMATION
 * ======================================================================== */
#loading {
  margin: auto;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999999;
}
#loading .loading-inner {
  position: absolute;
  top: 40%;
  margin: 0 auto;
  left: 0;
  right: 0;
  text-align: center;
}
#loading .loading-inner img {
  margin-right: auto;
  margin-left: auto;
}

/* ========================================================================
 * LOADING INDICATOR
 * ======================================================================== */
.indicator {
  position: absolute;
  z-index: 999;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 2px;
  cursor: progress;
}
.indicator.inline {
  display: block !important;
  position: relative;
  z-index: 9;
  height: 34px;
  background-color: transparent;
  border-radius: 0;
}
.indicator .spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -9px;
  margin-left: -9px;
  width: 18px;
  height: 18px;
  background-image: url("../../../assets/global/img/loader/general/1.gif");
  background-repeat: no-repeat;
}

/* ========================================================================
 * CHOOSE THEMES
 * ======================================================================== */
.themes {
  position: relative;
  z-index: 2;
  list-style: none;
  padding: 0px;
}
.themes li {
  height: auto;
  border: none;
  padding: 0;
  position: relative;
  float: left;
  margin-bottom: 5px;
}
.themes li a {
  opacity: 0.7;
  line-height: 32px;
  -moz-border-radius: 15px;
  -webkit-border-radius: 15px;
  border-radius: 15px;
  display: block;
  color: #676767;
  font-weight: 600;
  text-decoration: none;
  position: relative;
  font-size: 14px;
  padding: 5px !important;
  width: 17px;
  height: 17px !important;
  margin-right: 5px;
}
.themes li a:hover {
  opacity: 1;
}

